.TemplateForo{
    padding-top: 30px;
}

.Topic{
    text-align: left;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    padding:10px;
}

.Replies{
    margin-left:50px;
}

.reply{
    margin: 5px 0 0 0;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    padding:10px;
}
.reply-buttons{
    text-align:right;
}

textarea{
    resize: none;
    width:80%;
    margin: 20px 0 0 0;
}

.Topic-header{
    display:flex;
    flex-direction: row;
    position:relative;
}

.status{
    position:absolute;
    top:0;
    right:3em;
}
.status h3{
    margin:0;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}
.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}
input:checked + .slider {
    background-color: #54bb5d;
}

input:focus + .slider {
    box-shadow: 0 0 1px #54bb5d;
}
input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}